/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

:root {
  --nwcGreen: hsl(167, 100%, 20.2%);
  --nwcLightGreen: hsl(120, 55.5%, 44.9%);
  --nwcRed: hsl(355, 84.5%, 54.5%);
  --nwcYellow: hsl(47, 100%, 46.1%);
  --nwcBlue: hsl(220, 100%, 23.1%);
  --nwcLightBlue: hsl(207, 100%, 40.6%);
  --green: hsl(166, 100%, 18.4%);
  --lightgreen: hsl(247, 9.8%, 36.1%);
  --fadeRed: hsl(360, 82.5%, 57.5%);
  --brightRed: hsl(358, 72.6%, 45.9%);
  --red: hsl(358, 84.6%, 51.8%);
  --darkBlue: hsl(235, 52.2%, 9.0%);
  --gold: hsl(54, 69.2%, 84.7%);
  --lightGold: hsl(54, 71.4%, 97.3%);
  --black: hsl(0, 0%, 0.0%);
  --richBlack: hsl(0, 0%, 6%);
  --lightGrey: hsl(210, 1%, 60.8%);
  --grey: hsl(0, 0%, 50.2%);
}

:root {
  --font-body: "Lato", sans-serif;
  --font-display: "League Spartan", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-body);
  line-height: 1;
  min-height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  background-image: url("../img/countdown_bkgd_1920x1080-min.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}

.coming-soon {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 0.625rem;
}

.wpgLogo {
  margin-bottom: 1.25rem;
}
@media (max-width: 39.9375em) {
  .wpgLogo {
    margin-bottom: 2.5rem;
  }
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

h2 {
  font-size: 3.5rem;
  font-family: var(--font-display);
  font-weight: 700;
  margin-bottom: 0;
}

h3 {
  font-size: 2.25rem;
  font-family: var(--font-display);
  font-weight: 700;
}

.countdown {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
}

.cards {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
@media (max-width: 39.9375em) {
  .cards {
    flex-wrap: wrap;
  }
}
.cards__container {
  margin-bottom: 1.25rem;
  color: var(--nwcGreen);
}
@media (max-width: 39.9375em) {
  .cards__container {
    width: 50%;
  }
}
.cards__container--day h2, .cards__container--hour h2, .cards__container--minute h2, .cards__container--second h2 {
  padding: 0 3.5rem;
  margin-bottom: 0;
}
.cards__container--day h3, .cards__container--hour h3, .cards__container--minute h3, .cards__container--second h3 {
  padding: 0 3rem;
  margin-bottom: 1.125rem;
}